.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

#unity-canvas-1 {
  width: 100%;
  height: 100%;
}

.loading {
  width: 40%;
  height: 20px;
  background-color: rgb(0, 0, 0);
  position: absolute;
  top: 50%;
  left: 30%;
  z-index: 1;
  border-radius: 5px;
}

canvas {
  z-index: 2;
}

.percent {
  height: 20px;
  background-color: rgb(255, 255, 255);
  margin-top: 0px;
  border-radius: 5px;
  transition: 1s;
}